* {
    margin: 0; padding: 0 ; box-sizing: border-box;
}

.detailContainer {
    // border: solid 2px red;
    width: 640px;
    margin:3vh auto 0;
    .detailTitle {
        width: 100%;
        padding: 5vw;
        h3 {
            font-weight: bold;
        }
        .price {
            font-size: 3.5vh;
            .moto {
                font-size: 2.5vh;
                text-decoration: line-through;
                margin-right: 2vh;
            }
        }
    }
    .orderFixed {
        position: fixed;
        background: rgb(255, 224, 46);
        width: 100%; left: 0;
        top: 90vh;
        height: 10vh;
        margin: 0 auto;
        font-size: 4vh;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        z-index: 100;
        // display: none;
        &:hover {
            background: rgb(240, 211, 47);
        }
    }
    .detailIMG {
        // border: solid 2px red;
        width: 80%;
        margin: 0 auto;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        overflow: hidden;
        img {
            width: 100%;
        }
    }
}

.content {
    padding: 5vh;
    .contentTop {
        font-size: 2.5vh;
        padding: 4vh 0;
    }
    .blabla {
        padding-top: 4vh;
    }
}

.review {
    // border: solid 2px blue;
    padding: 2vh 0;
    li {
        width: 100%;
        // border: solid 2px blue;
        padding: 0;
        border-bottom: solid 1px rgb(211, 211, 211);
        padding:3vh 0;
        span {
            font-size: 2.5vh;
        }
        div {
            font-size: 2vh;
        }
    }
}

.reviewWrite {
    padding-bottom: 10vh;
    // border: solid 2px blue;
    input {
        padding: 2vh 3vw;
    }
    button {
        padding: 1vh 2vw;
        font-size: 2vh;
    }
}

.darara-enter {
    opacity: 0
}

// .dudun-enter {
//     display : none
// }

.darara-enter-active {
    opacity: 1;
    transition: all .5s;
}

.likeAlert {
    position:fixed;
    top:50%; left: 50%;
    transform: translate(-50%,-50%);
    border: solid 2px rgb(100, 100, 100);
    background: white;
    padding: 1vh 2vh;
}

@media all and (min-width:480px) and (max-width:639px) {
    // body { border: solid 5px red}
}

@media all and (min-width:480px) and (max-width:639px) {
    .detailContainer{ width: 100%; }
    .contentIMG img { width: 100%;}
}

@media all and (max-width:479px) {
    .detailContainer{ width: 100%; }
    .contentIMG img { width: 100%;}
    .content {
        contentTop { font-size: 2vh;}
    }
    .detailContainer .detailTitle h3 {
        font-size: 2.5vh;
    }
    .detailContainer .detailTitle .price {
        font-size: 2.5vh;
        .moto {
            font-size: 2vh;
        }
    }
    .detailContainer .detailTitle button {
        margin-top: 2.5vh;
    }
}