.cart {
    width: 640px;
    margin: 0 auto;
    ul {
        width: 640px;
        li {
            padding: 2vh 2vh;
            // border: solid 2px blue;
            width: 100%;
            height: auto;
            display: flex;
            align-items: center;
            img { width: 150px; height: 150px;}
            .cartContent {
                // border: solid 2px blue;
                position: relative;
                width: 50vh;
                text-align:left;
                padding: 1vh 2vh;
                font-size: 2.5vh;
                p {
                    margin: 0;
                    font-size: 1.8vh;
                }
                p:nth-child(3) {
                    font-size: 1.8vh;
                    font-weight: 400;
                }
                span:not(:nth-child(2)) {
                    font-size: 3.5vh;
                    font-weight: 600;
                }
                span {
                    margin: 0 1vh;
                }
            }
            input {
                width: 5vh;
                height: 5vh;
                margin-right: 2.5vh;
            }
        }
    }
}
.bill {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    padding: 0 2vh 15vh 2vh;
    p {
        margin: .5vh 0;
        display: flex;
        justify-content:space-between;
        text-align: right;
        .free {
            color:rgb(185, 185, 185);
        }
    }
    p:nth-child(3) {
        font-size: 2.5vh;
        margin-top: 2vh;
        font-weight: 600;
    }
}

.orderFix {
    position: fixed;
    background: rgb(255, 224, 46);
    width: 100%; left: 0;
    top: 91vh;
    height: 10vh;
    margin: 0 auto;
    font-size: 4vh;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 100;
    &:hover {
        background: rgb(240, 211, 47);
    }
}

.trash {
    width: 20px;
    display: flex;
    margin-left: 1vh;
    margin-bottom: 1.5vh; 
}

@media all and (min-width:640px) and (max-width:779px) {
    .cart {
        width: 100%;
        ul {
            width: 100%;
            li {
                padding: 2vh 0;
            }
        }
    }
  }

