* {
    margin:0; padding:0; box-sizing: border-box;
  }

  li,ul,ol {
    list-style: none;
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  a:visited {
    color: inherit
  }
  
  .App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .navBar {
    background: rgb(255, 224, 46);
    width: 100%;
    margin: 0;
    display : flex;
    justify-content: center;
    .navCon {
      width: 100%;
      margin: 0;
    }
  }

  .fixed {
    position: fixed;
    font-size: 2vh;
    left: 90%;
    background: rgb(255, 238, 144);
    border-radius: 50%;
    width: 60px; height: 60px;
    top: 10%;
    display : flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 999;
  }
  .fixedLike {
    top: 20%;
  }
  

  
  .jumbo {
    width: 640px;
    background-image: url("img/20210507135541454_8809721509012_ZW_00.jpg");
    height: 40vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 1vh auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
  }
  
  .jumbo h3 {
    font-size: 7vh;
  }
  

  
  
  .item {
    margin: 1px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    /* border: solid 2px grey; */
  }
  .item:hover .itemIMG {
    background-size: 110%;
  }
  .item1, .item7, .item13, .item19 {
    grid-column: span 4;
    grid-row: span 4;
  }
//   .item2 {
//     grid-column: span 2;
//     grid-row:span 2;
//   }
//   .item3 {
//     grid-column: span 2;
//     grid-row:span 2;
//   }
//   .item4 {
//     grid-column: span 2;
//     grid-row:span 2;
//   }
//   .item5 {
//     grid-column: span 2;
//     grid-row:span 2;
//   }
//   .item6 {
//     grid-column: span 2;
//     grid-row:span 2;
//   }

  @for $i from 1 through 24  {
      .item#{$i} .itemIMG{
          background-image: url(./data#{$i}.jpg);
      }
  }

  @for $i from 2 through 6 {
      .item#{$i} {
        grid-column: span 2;
        grid-row:span 2;
      }
  }

  @for $i from 8 through 12 {
    .item#{$i} {
      grid-column: span 2;
      grid-row:span 2;
    }
}

@for $i from 14 through 18 {
    .item#{$i} {
      grid-column: span 2;
      grid-row:span 2;
    }
}

@for $i from 20 through 24 {
    .item#{$i} {
      grid-column: span 2;
      grid-row:span 2;
    }
}
  
  
  
  .itemIMG {
    width: 100%; height: 100%;
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    transition: .5s;
  }
  
  
  .hoverBox {
    position: absolute;
    transform: translate(-50%,-50%);
    top: 150%; left: 50%;
    background: rgba(255, 255, 255, 0.26);
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: .3s ease-out;
  }
  .hoverBox h3 {
    font-size: 3vh;
  }
  
  Button {
    margin: 2vw;
    color: red;
  }

  footer {
    font-size: 1.5vh;
    margin-top: 1vh;
    padding: 2vh;
    color:rgb(119, 119, 119);
    background: rgb(241, 241, 241);
  }



  @media all and (min-width:640px) and (max-width:779px) {
    // body {border: solid 2px blue;}
    .jumbo {
      width: 100%;
    }
  }
  @media all and (min-width:480px) and (max-width:639px){
    // body {border: solid 2px red}
    .jumbo {
      width: 100%;
    }
  }

  @media all and (max-width:479px) {
    // body { border: solid 2px pink; width: 100%;}
    .jumbo {
      width: 100%;
    }
    .fixed {
      left: 84%;
    }
  }