* {
    margin: 0; padding: 0; box-sizing: border-box;
}

.title {
    font-size: 2vh;
    // border: solid 2px blue;
    padding: 2vh;
    color:rgb(68, 68, 68);
    background:rgb(241, 241, 241);
    width: 640px;
    margin: 2vh auto;
}

ul {
    width: 640px;
    padding: 0;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    li {
        position: relative;
        width: 100%;
        margin: 0 1vh;
        padding: 2vh 0;
        border-bottom: solid 1px rgb(224, 224, 224);
        display: flex;
        &:first-child {
            border-top: solid 1px rgb(224, 224, 224);
        }
        .itemPhoto {
            width: 100px; height: 100px;
            border-radius: 2vh;
            opacity: 1;
            transition: .5s;
            cursor: pointer;
            &:hover {
                opacity: 0.8;
            }
        }
        .itemContent {
            font-size: 1.8vh;
            padding: 0 1vh;
            width: 50%;
            text-align: left;
            cursor: pointer;
            .itemTitle {
                font-size: 1.8vh;
                margin-bottom: 0;
            }
            .price {
                margin: 0;
                .motoPrice {
                    text-decoration: line-through;
                    font-size: 1.5vh;
                }
                .salePrice {
                    font-size: 2.1vh;
                    font-weight: 600;
                    margin: 0 .7vh;
                }
            }
            .delete {
                position: absolute;
                top: 6%;
                left: 90%;
                font-size: 2vh;
                color:rgb(146, 146, 146);
                cursor: pointer;
            }
        }
    }
}

.ifDefault {
    width: 640;
    margin: 25vh auto;
    .please2 {
        width: 180px; height: 180px;
        margin-top: 2vh;
    }
}

@media all and (max-width:479px) {
    .title{
        width: 100%;
    }
    ul {
        width: 100%;
    }
    ul li .itemContent .delete {
        top: 60%;
    }
}